import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';

import { EntityM, EntityTreeM, EntityTypeM } from '../model/entity.model';

@Injectable({
    providedIn: 'root'
})

export class EntityController {

    constructor(
        private http: HttpClient,
        @Inject('environment') private environment: any
    ) { }

    getEntitiesByOrgID(orgID): Observable<EntityM[]> {
        // return this.http.get<EntityM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/entities`);
        return this.http.get<EntityM[]>(`${this.environment.MOCK_SERVER}entities`);
    }

    getEntityHierarchyByParameterID(orgID, parameterID): Observable<EntityTreeM[]> {
        return this.http.get<EntityTreeM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/parameters/${parameterID}/entities`);
    }

    getEntityLevelConsumption(orgID, requestBody) {
        return this.http.post(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/entities/load-consumption`, requestBody);
    }

    getEntityTypeByLayoutID(orgID, skipLoader): Observable<EntityTypeM[]> {
        let headers = new HttpHeaders().set('skipLoader', skipLoader);

        return this.http.get<EntityTypeM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/entities/type`, { headers: headers });
    }

    getEntitiesWithDevices(orgID, layoutID, requestBody, skipLoader): Observable<EntityTreeM> {
        let headers = new HttpHeaders().set('skipLoader', skipLoader);

        return this.http.post<EntityTreeM>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entity-device`, requestBody, { headers: headers });
    }

    getEntityByParameterID(orgID, requestBody): Observable<EntityM[]> {
        return this.http.post<EntityM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/entities`, requestBody);
        // return this.http.get<EntityM[]>(`${this.environment.MOCK_SERVER}entities`);
    }

    getEntitiesForTreeView(orgID, layoutID): Observable<EntityTreeM[]> {
        return this.http.get<EntityTreeM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entities`);
    }

    getEntityByID(orgID, layoutID, entityID): Observable<EntityM> {
        return this.http.get<EntityM>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entities/${entityID}`);
    }

    createEntity(orgID, layoutID, entityID, entity) {
        return this.http.post(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entities/${entityID}/add-child`, entity);
    }

    updateEntity(orgID, layoutID, entityID, entity) {
        return this.http.patch(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entities/${entityID}`, entity);
    }

    deleteEntity(orgID, layoutID, entityID) {
        return this.http.delete(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/${layoutID}/entities/${entityID}`)
    }
}
