import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs';

import { DevicesWithEntitiesM } from '../model/device.model';

@Injectable({
    providedIn: 'root'
})

export class EntityDeviceController {

    constructor(
        private http: HttpClient,
        @Inject('environment') private environment: any
    ) { }

    //to chnge endpoint
    getDevicesWithEntities(orgID, requestBody):Observable<DevicesWithEntitiesM[]> {
        return this.http.post<DevicesWithEntitiesM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/layouts/entities/devices`, requestBody);
    }

    //TODO: SOMYA AGRAWAL: To discuss this api with Abhinav
    getDevicesWithEntitiesByParameter(orgID, paramMetric, skipLoader): Observable<DevicesWithEntitiesM[]> {
        let headers = new HttpHeaders().set('skipLoader', skipLoader);

        let params = new HttpParams();
        params = params.append('paramMetric', paramMetric);
        return this.http.post<DevicesWithEntitiesM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/entities-devices`, { params: params, headers: headers });
    }
}
