export interface DeviceM {
    id: number;
    name: string;
    sourceOrLoad: string;
    sourceLoadType: string;
    status: string;
}

export class Device implements DeviceM {
    id;
    name;
    sourceOrLoad;
    sourceLoadType;
    status;
}

export interface DeviceModel {
    id: number;
    name: string;
    sourceOrLoad: string;
    sourceLoadType: string;
}

export interface DevicesWithEntitiesM {
    id: number;
    name: string;
    type: string;
    devices: DeviceModel[];
}

export class DevicesWithEntities implements DevicesWithEntitiesM {
    id;
    name;
    type;
    devices;
}

export interface DeviceInfoM {
    total: number;
    online: number;
    delay: number;
    offline: number;
}

export class DeviceInfo implements DeviceInfoM {
    total;
    online;
    delay;
    offline;
}

