import { Injectable } from '@angular/core';

import { Entity, EntityM, EntityTree, EntityTreeM, EntityType, EntityTypeM } from '../model/entity.model';

import { EntityController } from '../controller/entity.controller';

@Injectable({
    providedIn: 'root'
})

export class EntityService {

    constructor(
        private entityController: EntityController
    ) { }

    async getEntitiesByOrgID(orgID): Promise<EntityM[]> {
        let entityM: EntityM[];
        try {
            entityM = await this.entityController.getEntitiesByOrgID(orgID).toPromise();

            if (entityM) {
                return entityM;
            } else {
                return [new Entity()];
            }
        } catch (error) {
            console.error('Error -', error);
            return [new Entity()];
        }
    }

    async getEntityHierarchyByParameterID(orgID, parameterID): Promise<EntityTreeM[]> {
        let entityTreeM: EntityTreeM[];
        try {
            entityTreeM = await this.entityController.getEntityHierarchyByParameterID(orgID, parameterID).toPromise();

            if (entityTreeM) {
                return entityTreeM;
            } else {
                return [new EntityTree()];
            }
        } catch (error) {
            console.error('Error -', error);
            return [new EntityTree()];
        }
    }

    async getEntityTypeByLayoutID(orgID, skipLoader): Promise<EntityTypeM[]> {
        let entityTypeM: EntityTypeM[];
        try {
            entityTypeM = await this.entityController.getEntityTypeByLayoutID(orgID, skipLoader).toPromise();

            if (entityTypeM) {
                return entityTypeM;
            } else {
                return [new EntityType()];
            }
        } catch (error) {
            console.error('Error -', error);
            return [new EntityType()];
        }
    }

    async getEntitiesWithDevices(orgID, layoutID, requestBody, skipLoader): Promise<EntityTreeM> {
        let entityDeviceM: EntityTreeM;
        try {
            entityDeviceM = await this.entityController.getEntitiesWithDevices(orgID, layoutID, requestBody, skipLoader).toPromise();

            if (entityDeviceM) {
                return entityDeviceM;
            } else {
                return new EntityTree();
            }
        } catch (error) {
            console.error('Error -', error);
            return new EntityTree();
        }
    }

    async getEntityByParameterID(orgID, requestBody): Promise<EntityM[]> {
        let entityM: EntityM[];
        try {
            entityM = await this.entityController.getEntityByParameterID(orgID, requestBody).toPromise();

            if (entityM) {
                return entityM;
            } else {
                return [new Entity()];
            }
        } catch (error) {
            console.error('Error -', error);
            return [new Entity()];
        }
    }

}