import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl } from '@angular/forms';

import { EChartsOption } from 'echarts';

import { EntityTree, EntityTreeM } from '../../../model/entity.model';

import { EntityService } from '../../../service/entity.service';

// tsc-library
import { StorageService } from '@library/storage-service';
import { Layout, LayoutService } from '@library/layout-service';
import { FormAction } from '@library/tsc-common';
// /tsc-library/


@Component({
    selector: 'lib-view',
    templateUrl: './view.component.html',
    styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {

    formActionEnum = FormAction;

    orgID: any;
    qParams: any;
    layoutID: any;

    layoutM = new Layout();
    entityDeviceM = new EntityTree();

    allowUpdateEntityTree: boolean = false;
    chartDataOption: EChartsOption;

    selectedViewFC = new FormControl('LAYOUT');

    constructor(
        private activatedRoute: ActivatedRoute,
        private storageService: StorageService,
        private layoutService: LayoutService,
        private entityService: EntityService,
    ) { }

    ngOnInit(): void {
        this.orgID = this.storageService.getStorage('currentOrgID');

        this.activatedRoute.params.subscribe((param) => {
            this.layoutID = param['layoutID'];
            this.getLayoutByID();
            this.getEntitiesWithDevices();
        });
    }

    async getLayoutByID() {
        this.layoutM = await this.layoutService.getLayoutByID(this.orgID, this.layoutID);
    }

    async getEntitiesWithDevices() {
        let requestBody = {
            'entityType': ['ALL']
        };

        this.entityDeviceM = <EntityTreeM>await this.entityService.getEntitiesWithDevices(this.orgID, this.layoutID, requestBody, 'NO');

        if (this.entityDeviceM) {
            this.drawChart();
        }
    }

    changeState() {
        this.allowUpdateEntityTree = true;
    }

    drawChart() {
        const convertNode = (node) => ({
            name: node.name,
            children: node.children ? node.children.map(convertNode) : [],
        });
        const chartData = convertNode(this.entityDeviceM);

        this.chartDataOption = {
            tooltip: {
                trigger: 'item',
                triggerOn: 'mousemove'
            },
            series: [
                {
                    type: 'tree',
                    id: 0,
                    name: 'tree1',
                    data: [chartData],
                    top: '10%',
                    left: '15%',
                    bottom: '22%',
                    right: '15%',
                    symbolSize: 7,
                    edgeShape: 'polyline',
                    edgeForkPosition: '63%',
                    initialTreeDepth: 999999,
                    lineStyle: {
                        width: 2
                    },
                    label: {
                        backgroundColor: '#fff',
                        position: 'left',
                        verticalAlign: 'middle',
                        align: 'right'
                    },
                    leaves: {
                        label: {
                            position: 'right',
                            verticalAlign: 'middle',
                            align: 'left'
                        }
                    },
                    emphasis: {
                        focus: 'descendant'
                    },
                    expandAndCollapse: true,
                    animationDuration: 550,
                    animationDurationUpdate: 750
                }
            ]
        }

    }

}
