export interface RecentDataRequestWithParamM {
    params: Array<{
        name: string;
    }>,
    deviceID: number[];
    includeDescendants: boolean;
    timezone: string;
    entityType: string;
    backscan: number;
}

export class RecentDataRequestWithParam implements RecentDataRequestWithParamM {
    params = null;
    deviceID = null;
    includeDescendants = false;
    timezone = null;
    entityType = null;
    backscan = null;
}

export interface RecentDataRequestWithoutParamM {
    entityID: number[];
    timezone: string;
    backscan: number;
}

export class RecentDataRequestWithoutParam implements RecentDataRequestWithoutParamM {
    entityID = null;
    timezone = null;
    backscan = null;
}

export interface ParamData {
    name: string;
    label: string;
    unit: string;
    data: {
        date: string;
        value: number;
    };
}

export interface DeviceData {
    id: number;
    name: string;
    status: string;
    paramData: ParamData[];
}

export interface OrgRecentDataM {
    orgID: number;
    entityID: number;
    entityName: string;
    entityType: string;
    deviceData: DeviceData[];
}

export class OrgRecentData implements OrgRecentDataM {
    orgID;
    entityID;
    entityName;
    entityType;
    deviceData;
}