export interface EntityM {
    id: number;
    name: string;
    type: string;
}

export class Entity implements EntityM {
    id;
    name;
    type;
}

export interface ChildEntity {
    id: number;
    name: string;
    type: string;
    children: ChildEntity[];
}

export interface EntityTreeM {
    id: number;
    name: string;
    type: string;
    isParameterExists: boolean;
    children: ChildEntity[];
}

export class EntityTree implements EntityTreeM {
    id;
    name;
    type;
    isParameterExists;
    children;
}

export interface EntityLevelConsumptionM {
    id: number;
    name: string;
    value: number;
    unit: string;
}

export class EntityLevelConsumption implements EntityLevelConsumptionM {
    id;
    name;
    value;
    unit;
}

export interface EntityTypeM {
    type: string;
}

export class EntityType implements EntityTypeM {
    type;
}