import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs';

import { DeviceInfoM, DeviceM, DevicesWithEntitiesM } from '../model/device.model';

@Injectable({
    providedIn: 'root'
})

export class DeviceController {

    constructor(
        private http: HttpClient,
        @Inject('environment') private environment: any
    ) { }

    getDeviceInfo(orgID): Observable<DeviceInfoM> {
        return this.http.get<DeviceInfoM>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/devices/status`);
    }

    getDevicesByEntityID(orgID, requestBody): Observable<DeviceM[]> {
        return this.http.post<DeviceM[]>(`${this.environment.TSC_COMMON_API_URL}organizations/${orgID}/entities/devices`, requestBody);
        // return this.http.get<DeviceM[]>(`${this.environment.MOCK_SERVER}devices`);
    }

}
